.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
  text-decoration: none;
}

.li-style{
  list-style-type: none;
  width:200px;
  padding: 5px;
  border: 1px solid gray;
}
.ul-style{
  display: flex;
}

.button-style:disabled{
  background: #09d3ac;
}